import React from 'react'
import styles from './Slowtecture.module.css';
import Container from '../../../components/Container/Container';


export default function Slowtecture() {
  return (
    <Container>
    <div className={styles["text"]}><h1>SLOWTECTURE</h1>
    <p>Our driving force is to be the facilitator of houses built by hand by skilled craftsmen with traces of the human hand. Houses built to last for at least 500 years to an ever-increasing beauty. And the only thing needed is lots of love and the scarce commodity we call time. </p>
    <p>Therefore we call it Slowtecture.</p>
    <p>To explore the world of Slowtecture please feel free to contact us for a lecture on <a style={{color: '#595959'}} href="mailto:slowtecture@cwarc.se">slowtecture@cwarc.se.</a>  </p>
    <p>Non private kindergartens, schools, universities and non-profit organizations free of charge. </p>
    <p>All other parties price upon request. </p>
    <p>No online lectures offered. </p>
    <p>No flights. Fossil-free travels only.</p>
    </div>
    </Container>
  )
}
